import { React, useState } from "react";
import { Link, withRouter } from "react-router-dom";

import '../styles/styles.css';

import pumpkinspiceroll_none from '../images/pumpkinspice.jpg';
import pumpkinspiceroll_sugar from '../images/pumpkinspice-sugar.jpg';
import pumpkinspiceroll_vanilla from '../images/pumpkinspice-vanilla.jpg';
import pumpkinspiceroll_chocolate from '../images/pumpkinspice-chocolate.jpg';

function Cart(props) {
  return (
    <div id="cart-container">
      <body className="standard">

        <div className="standard_content">
          <div className="heading">
              <h1>Your Cart: {props.cart_total} items</h1>
          </div>
{/*4 column grid to hold different details about item i.e. description, count, total price*/}

  <div>
    {props.cart_items.map((item, index) => (
      <div className="row">
      {/*delete an item*/}
        <button className="delete" onClick={() => {props.onItemDelete(item.glaze, item.quantity)}}>x</button>
        <div className="column space">
        {/*show which image to display based on glaze*/}
          <img className={item.glaze === "None" ? "cart_image" : "notselectedimage" } src={pumpkinspiceroll_none} alt="pumpkinspiceroll"/>
          <img className={item.glaze === "Sugar" ? "cart_image" : "notselectedimage" } src={pumpkinspiceroll_sugar} alt="pumpkinspiceroll_sugar"/>
          <img className={item.glaze === "Vanilla" ? "cart_image" : "notselectedimage" } src={pumpkinspiceroll_vanilla} alt="pumpkinspiceroll_vanilla"/>
          <img className={item.glaze === "Chocolate" ? "cart_image" : "notselectedimage" } src={pumpkinspiceroll_chocolate} alt="pumpkinspiceroll_chocolate"/>
        </div>
        {/*item description*/}
        <div className="column space">
          <p id="item_name">{item.rollname}</p>
          <p id="item_glaze">Glaze: {item.glaze}</p>
          <p id="item_price">${item.unitprice}/roll</p>
        </div>

        <div className="column quant_col space">
          <p id="quant_title">Quantity</p>
          <div className="row quant_row">
          {/*adjust quantity*/}
            <button className="column quant_button" onClick={() => {props.onItemMinus(item.glaze)}}>-</button>
            <p  className="column" id="item_number">{item.quantity}</p>
            <button className="column quant_button" onClick={() => {props.onItemPlus(item.glaze)}}>+</button>
          </div>
        </div>

        <div className="column quant_col">
          <p id="quant_title">Total Price</p>
          <p id="item_number">${item.price}</p>
        </div>
      </div>
    ))}
  </div>

        </div>
      </body>
    </div>
  );
}

export default Cart;
