import { React, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Navigation from "./components/Navigation.js";
import Home from "./components/Home.js";
import Products from "./components/Products.js";
import OurStory from "./components/OurStory.js";
import PumpkinSpice from "./components/PumpkinSpice.js";
import GlutenFree from "./components/GlutenFree.js";
import OriginalCinnamon from "./components/OriginalCinnamon.js";
import Walnut from "./components/Walnut.js";
import Blackberry from "./components/Blackberry.js";
import CaramelPecan from "./components/CaramelPecan.js";
import Cart from "./components/Cart.js";

import './styles/styles.css';

function App() {
  {/*setting up list to hold items and and const to hold total number of items*/}
  const [shoppingCart, setshoppingCart] = useState([]);
  const [total, setTotal] = useState(0);

{/*function that removes an item from the shopping cart*/}
  let ItemDelete = function(glaze_type, delnum) {
    var j;
    for (j = 0; j < shoppingCart.length; j++) {
      if(shoppingCart[j].glaze == glaze_type) {
        shoppingCart.splice(j, 1);
        setshoppingCart(shoppingCart);
        setTotal(total - delnum);
      }
    }
  }

{/*function that decreases quantity of an item in the cart*/}
  let ItemMinus = function(glaze_type) {
    var k;
    for (k = 0; k < shoppingCart.length; k++) {
      if(shoppingCart[k].glaze == glaze_type) {
        shoppingCart[k].quantity -= 1;
        var price_num = shoppingCart[k].unitprice * shoppingCart[k].quantity;
        shoppingCart[k].price = price_num.toFixed(2);
        setshoppingCart(shoppingCart);
        setTotal(total - 1);
        if (shoppingCart[k].quantity == 0) {
          shoppingCart.splice(k, 1);
          setshoppingCart(shoppingCart);
        }
      }
    }
  }

{/*function that increases quantity of an item in the cart*/}
  let ItemPlus = function(glaze_type) {
    var m;
    for (m = 0; m < shoppingCart.length; m++) {
      if(shoppingCart[m].glaze == glaze_type) {
        shoppingCart[m].quantity += 1;
        var price_num = shoppingCart[m].unitprice * shoppingCart[m].quantity;
        shoppingCart[m].price = price_num.toFixed(2);
        setshoppingCart(shoppingCart);
        setTotal(total + 1);
      }
    }
  }

{/*function that adds new products to the shopping cart*/}
  let AddToCart = function(glaze, quantity, rollname, price, unitprice) {

    var i;
    var match = "none";
    for (i = 0; i < shoppingCart.length; i++) {
      if(shoppingCart[i].glaze == glaze) {
        shoppingCart[i].quantity += parseInt(quantity);
        shoppingCart[i].price += price*quantity;
        match = "found";
      }
    }

{/*adding items to list and increasing number of items*/}
  if (match == "none") {
    shoppingCart.push({
      glaze: glaze,
      quantity: parseInt(quantity),
      rollname: rollname,
      price: price*quantity,
      unitprice: unitprice
    })
  }

    setTotal(total + parseInt(quantity));
    setshoppingCart(shoppingCart);

    console.log(shoppingCart);
    console.log ("Cart Total: ", total);
  }

  return (
    <div className="App">
      <Router>
        <Navigation cart_total={total}/>
        <Switch>
          <Route path="/" exact component={() => <Home />} />
          <Route path="/ourstory" exact component={() => <OurStory />} />
          <Route path="/products" exact component={() => <Products />} />
          <Route path="/pumpkinspice" exact component={() => <PumpkinSpice onAddToCart={AddToCart}/>} />
          <Route path="/glutenfree" exact component={() => <GlutenFree />} />
          <Route path="/originalcinnamon" exact component={() => <OriginalCinnamon />} />
          <Route path="/walnut" exact component={() => <Walnut />} />
          <Route path="/blackberry" exact component={() => <Blackberry />} />
          <Route path="/caramelpecan" exact component={() => <CaramelPecan />} />
          <Route path="/cart" exact component={() => <Cart onItemMinus={ItemMinus} onItemPlus={ItemPlus} onItemDelete={ItemDelete} cart_total={total} cart_items={shoppingCart}/>} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
